.banner {
  width: 100%;
  height: 300px;
  background-color: pink;
  display: flex;
  align-items: center;
  justify-content: center;
}

.links a {
  margin: 0 0.75rem;
  color: #0366d6;
  text-decoration: none;
  font-weight: bold;
  font-size: 1rem;
}

.links a:hover {
  text-decoration: underline;
}